.btn a {
  display: block;
  border-radius: 10px;
  padding: 15px 30px;
}

.btn a span {
  position: relative;
  padding-right: 22px;
  display: inline-block;
  width: 100%;
}

.btn a span::before {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  border-radius: 50%;
  top: 50%;
  right: 0;
  margin-top: -7px;
}

.btn a span::after {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  border: solid 4px transparent;
  top: 50%;
  right: 0;
  margin-top: -3px;
}

@media only screen and (min-width: 1000px) {
  .btn a span::after {
    margin-top: -7px;
  }
}
.btn a:hover {
  opacity: 0.7;
}

