/*---------- Project ----------*/
.p-header {
  background-color: #fff;
  width: 100%;
  transition: 1.2s;
  position: fixed;
  z-index: 5;
  top: 0;
}

.p-header__logo {
  width: 14rem;
  transition: all 0.3s linear;
}

@media only screen and (min-width: 1000px) {
  .p-header__logo {
    width: 19.5rem;
  }
}
.p-header__logo:hover {
  filter: brightness(1.2);
}

.p-header__txt {
  font-size: 1rem;
  line-height: 1.2;
  margin-left: 1rem;
  font-weight: 500;
  color: #555;
}

@media only screen and (min-width: 768px) {
  .p-header__txt {
    font-size: 1.4rem;
    line-height: 1.5;
  }
}
@media only screen and (min-width: 1000px) {
  .p-header__txt {
    margin-left: 2rem;
    margin-right: 1rem;
  }
}
.p-header__btn {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 7;
  width: 4.5rem;
  cursor: pointer;
  pointer-events: auto;
  touch-action: manipulation;
  margin-left: auto;
  background-color: #000000;
  padding: 0.9rem 0rem 0.5rem;
  text-align: center;
}

@media only screen and (min-width: 768px) {
  .p-header__btn {
    width: 5rem;
  }
}
@media only screen and (min-width: 1000px) {
  .p-header__btn {
    display: none;
  }
}
.p-header__btn span {
  display: block;
  width: 2.25rem;
  height: 0.2rem;
  margin-right: auto;
  margin-left: auto;
  background-color: #fff;
  transition: all 0.3s linear;
}

.p-header__btn span + span {
  margin-top: 0.5rem;
}

.p-header__btn p {
  text-align: center;
  color: #fff;
  font-size: 1rem;
  margin-top: 0.5rem;
  line-height: 1;
  letter-spacing: -1px;
}

.p-header__btn.active {
  background-color: #fff;
}

.p-header__btn.active span {
  background-color: #000000;
}

.p-header__btn.active span:nth-of-type(1) {
  transform: translate3d(0, 7px, 0) rotate(45deg);
}

.p-header__btn.active span:nth-of-type(2) {
  opacity: 0;
}

.p-header__btn.active span:nth-of-type(3) {
  transform: translate3d(0, -7px, 0) rotate(-45deg);
}

.p-header__btn.active p {
  color: #000000;
}

.p-header__conversion {
  display: none;
}

@media only screen and (min-width: 1000px) {
  .p-header__conversion {
    border-left: 0.1rem solid #bbbbbb;
    display: flex;
    margin-left: auto;
    width: 51rem;
  }
}
.p-header__conversion li {
  width: 33.3333333333%;
  text-align: center;
  border-right: 0.1rem solid #bbbbbb;
}

.p-header__conversion li:nth-of-type(1) a img {
  width: 2rem;
  height: 2rem;
}

.p-header__conversion li:nth-of-type(2) a img {
  width: 1.6rem;
  height: 2rem;
}

.p-header__conversion li:nth-of-type(3) a img {
  width: 2rem;
  height: 1.6rem;
}

.p-header__conversion li a {
  transition: all 0.3s linear;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1.2;
  color: #333;
  min-height: 6rem;
  background-color: #fff;
  text-align: left;
  height: 8.5rem;
  font-size: 1.6rem;
  gap: 1rem;
  position: relative;
}

